@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600&display=swap);
html, body {
  margin: 0;
}

.App {
  --main-blue: #004B89;

  font-family: 'Nunito', sans-serif;
  /* margin: 0; */
}
.LoginPage {
    display: flex;
    flex-direction: row;
    font-family: 'Nunito Sans', sans-serif;
    min-height: 100vh;
}

.login-left, .login-right {
    width: 50%;
}

.login-left {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* margin: 10vh 10vw; */
    text-align: center;
}

.login-left img {
    margin-top: 20%;
    width: 50px;
}

.login-right {
    background-color: var(--main-blue);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-right img {
    width: 73%;
    margin-top: -10vh;
}

.login-right h1 {
    color: white;
    font-weight: 500;
    margin-top: 5vh;
    padding:0;
    z-index: 2;
    
}

.underline-text {
    font-size: 24px;
}

.underline-text:after {
    content: '';
    width: 60px;
    height: 4px;
    background: var(--main-blue);
    position: absolute;
    margin-top: 32px;
    left: 25%;
  }

  #login-btn {
      margin-top: 10%;
  }

  .LoginPage .Button {
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 600;
  }

  .temp-menu {
      margin: 20px;
  }
  .temp-menu a {
    color: white;
    text-decoration: none;
    font-weight: 600;
    margin: 10px 20px;
    font-size: 18px;
  }
.Button {
    padding: 16px 48px;
    background: var(--main-blue);
    border-radius: 5px;
    color: white;
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
    border: none;
    /* font-family: 'Nunito', sans-serif; */
}


